@import url(https://fonts.googleapis.com/css?family=Varela+Round&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: rgb(241, 241, 241);
  font-family: 'Varela Round', sans-serif;

}

.App {
  display: flex;
  flex-wrap: wrap;
  margin: auto;
  text-align: center;
  width: 90vw;
  height: 100vh;
}

h1 {
  padding-left: 20%;
  line-height: 80px;
  margin: 0;
  color: #fff;
  background-color: #90cf92;
  border-bottom: 5px solid #4bac4e;
}

.App div.container {
  border-radius: 10px;
  margin: auto;
  margin-top: 30px;
  position: relative;
  width: 80%;
  height: 400px;
  box-shadow: 4px 4px 11px 2px rgba(201, 201, 201, 1);
}

.App div.container .title {
  position: absolute;
  top: 0;
  left: 0;
  text-align: center;
  width: 100%;
}

.App div.container.currency {
  position: relative;
  width: 80%;
  height: 2000px;
}
.App div.container.geo {
  position: relative;
  width: 80%;
  height: 600px;
}

.App div.summary{
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}


.container {
  height: 1000px;
}


